import React, { useEffect } from "react";
import { connect } from "react-redux";
import { currentPage } from "../../store/actions";
import { Flex, Box, Text, Paragraph } from "@coreym/benchmark";
import CareLists from "./CareLists";
import { motion } from "framer-motion";
import CarePersons from "./CarePersons";
import MotionBox from "../../motions/motionBox/MotionBox";

const tempVariants = {
  initial: {
    opacity: 0,
  },
  animate: {
    opacity: 1,
    transition: {
      delay: 0.5,
    },
  },
  exit: {
    opacity: 0,
    // transition: {
    //   duration: 1,
    // },
  },
};

function CaretakerPerson(props) {
  const { homeNumber, homeTitle, currentPage, careSteps } = props;
  const { step } = careSteps;
  const showText = step === 0 ? true : false;

  useEffect(() => {
    currentPage({
      stage: 3,
      page: "caretakerperson",
      mapProps: props,
    });
  }, []);

  let page = null;
  switch (homeNumber.number) {
    case "1":
      page = (
        <>
          <Flex justifyContent="center">
            <CarePersons
              id="home1"
              title={homeTitle.home1}
              className="home3"
            ></CarePersons>
          </Flex>
        </>
      );
      break;
    case "2":
      page = (
        <>
          <Flex spacing="1">
            <CarePersons
              id="home1"
              title={homeTitle.home1}
              className="home3"
            ></CarePersons>
            <CarePersons
              id="home2"
              title={homeTitle.home2}
              className="home3"
            ></CarePersons>
          </Flex>
        </>
      );
      break;
    default:
      break;
  }
  return (
    <MotionBox
      variants={tempVariants}
      initial="initial"
      animate="animate"
      exit="exit"
    >
      {showText && (
        <MotionBox p="5">
          <Flex justifyContent="center">
            <Paragraph>
              Now we'll ask some questions about
              <br />
              the people whoe take care of you.
              <br />
              <Text>Tap </Text>
              <Text fontWeight="bold">Next</Text>
              <Text> to continue</Text>
            </Paragraph>
          </Flex>
        </MotionBox>
      )}
      <Box p="4">{page}</Box>
    </MotionBox>
  );
}

const mapStateToProps = (state) => {
  return {
    homeNumber: state.homeNumber,
    homeTitle: state.updateTitle,
    careSteps: state.showRadio,
  };
};

const mapDispatchtoProps = (dispatch) => {
  return {
    currentPage: (curPage) => dispatch(currentPage(curPage)),
  };
};

export default connect(mapStateToProps, mapDispatchtoProps)(CaretakerPerson);
