import React, { Component } from "react";
import { connect } from "react-redux";
import Stage from "./Stage";
import { uuid } from "uuidv4";
import NavIcon from "./NavIcon";
import { Flex, Box, Text } from "@coreym/benchmark";
import Bar from "./Bar";

class Navbar extends Component {
  render() {
    const { stage } = this.props.currentPage;
    let one = "1",
      two = "1",
      three = "1",
      four = "1",
      lineOne = "grey",
      lineTwo = "grey",
      lineThree = "grey";
    if (stage) {
      switch (stage) {
        case 1:
          one = "2";
          two = "1";
          three = "1";
          four = "1";
          lineOne = "grey";
          lineTwo = "grey";
          lineThree = "grey";
          break;
        case 2:
          one = "3";
          two = "2";
          three = "1";
          four = "1";
          lineOne = "blue";
          lineTwo = "grey";
          lineThree = "grey";
          break;
        case 3:
          one = "3";
          two = "3";
          three = "2";
          four = "1";
          lineOne = "blue";
          lineTwo = "blue";
          lineThree = "grey";
          break;
        case 4:
          one = "3";
          two = "3";
          three = "3";
          four = "2";
          lineOne = "blue";
          lineTwo = "blue";
          lineThree = "blue";
          break;

        default:
          break;
      }
    }

    const stages = [
      {
        s: 1,
        c: "Where you live",
      },
      {
        s: 2,
        c: "Your family",
      },
      {
        s: 3,
        c: "Your caregivers",
      },
      {
        s: 4,
        c: "Your summary",
      },
    ];
    let nav_content;
    if (stage) {
      nav_content = stages.map((s) => {
        if (s.s === stage) {
          s.link = "img/nav-on.png";
          s.color = "#0175d2";
        } else if (s.s < stage) {
          s.link = "img/nav-completed.png";
          s.color = "#0175d2";
        } else {
          s.link = "img/nav-uncompleted.png";
          s.color = "grey";
        }
        return (
          <Stage key={uuid()} color={s.color} link={s.link} content={s.c} />
        );
      });
    }

    return (
      // <div className="nav">
      // <div>
      //   {/* {nav_content} */}
      //   <NavIcon status="2" text="Where you live" />
      // </div>
      <Flex pt={3} justifyContent="center">
        <NavIcon status={one} text="Where you live" />
        <Bar color={lineOne} />
        <NavIcon status={two} text="Your family" />
        <Bar color={lineTwo} />
        <NavIcon status={three} text="Your caretakers" />
        <Bar color={lineThree} />
        <NavIcon status={four} text="Summary" />
      </Flex>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    currentPage: state.currentPage,
  };
};

export default connect(mapStateToProps)(Navbar);
