import React, { useEffect } from "react";
import { Switch, Route, useLocation } from "react-router-dom";
import Navbar from "./components/nav/Nav";
import Home from "./components/pages/home/Home";
import "font-awesome/css/font-awesome.min.css";
import ChoseHome from "./components/pages/choseHome/ChoseHome";
import PreDndHome from "./components/pages/dndHome/PreDndHome";
import DndHome from "./components/pages/dndHome/DndHome";
import FamilyAPI from "./components/api/FamilyAPI";
import ToolbarApp from "./components/nav/ToolbarApp";
import { GlobalProvider } from "@coreym/benchmark";
import EndPage from "./components/pages/Endpage";
import YourFamily from "./components/pages/yourFamily/YourFamily";
import Caretakers from "./components/pages/caretakers/Caretakers";
import { AnimatePresence } from "framer-motion";
import CaretakerPerson from "./components/pages/caretakers/CaretakerPerson";
import Summary from "./components/pages/summary/Summary";

function App(props) {
  const location = useLocation();
  useEffect(() => {
    const family_structure = new FamilyAPI();
    family_structure.init();
  }, []);

  return (
    <div className="App">
      {/* <Global /> */}
      <GlobalProvider>
        <ToolbarApp />
        <Navbar className="nav" />
        <div>
          <AnimatePresence>
            <Switch location={location} key={location.key}>
              <Route exact path="/" component={Home} />
              <Route path="/choseHome" component={ChoseHome} />
              <Route path="/predndHome" component={PreDndHome} />
              <Route path="/dndHome" component={DndHome} />
              <Route path="/yourfamily" component={YourFamily} />
              <Route path="/caretakers" component={Caretakers} />
              <Route path="/caretakerperson" component={CaretakerPerson} />
              <Route path="/summary" component={Summary} />
              <Route path="/end" component={EndPage} />
            </Switch>
          </AnimatePresence>
        </div>
      </GlobalProvider>
    </div>
  );
}

export default App;
