import React from "react";
import { useDrop } from "react-dnd";
import { ItemTypes } from "./ItemTypes";
import { connect } from "react-redux";
import { currentList, unlockNext } from "../store/actions";
// import { currentListReducer } from "../store/reducers";
import DragItem from "./DragItem";
import { uuid } from "uuidv4";
// import { filterDuplicate } from "../common/commons";
// import PropTypes from "prop-types";
import { checkLock, changeIconLabel } from "../common/commons";
import Title from "../title/Title";

const DropItem = (props) => {
  const {
    currentPage,
    homeNumber,
    currentList,
    updateList,
    unlockNext,
    title,
    border,
    id,
    over,
    className,
  } = props;

  const curPage = currentPage.page;
  let dragContainer = "draggers";
  if (curPage === "dndHome" && homeNumber.number === "2")
    dragContainer = "draggers2";
  let tabIndex = 1;

  if (id === "draggers2") tabIndex = 100;
  if (id === "home1") tabIndex = 200;
  if (id === "home2") tabIndex = 300;

  let container = [...currentList.container];

  const [{ isOver }, drop] = useDrop({
    accept: ItemTypes.CARD,
    drop: (item, monitor) => {
      if (item.name.toLowerCase() === "me") return;

      if (id !== dragContainer) {
        if (item.container === dragContainer) {
          const newItem = {};
          newItem.id = uuid();
          newItem.name = item.name;
          newItem.url = item.url;
          newItem.container = id;
          newItem.name = changeIconLabel(newItem, container);
          container.push(newItem);
        }
      } else {
        if (item.container !== dragContainer) {
          const container2 = container.filter(
            (dragger) => dragger.id !== item.id
          );
          container = container2;
        }
      }

      if (curPage === "predndHome")
        checkLock(container, homeNumber, unlockNext, "predndHome");
      else checkLock(container, homeNumber, unlockNext);
      updateList({
        container,
      });
    },
    collect: (monitor) => ({
      isOver: monitor.isOver(),
    }),
  });

  let border2 = border;
  if (border2 !== "none") {
    border2 = isOver ? over : border2;
  }

  const deleteIcon = (id) => {
    const container2 = container.filter((dragger) => dragger.id !== id);
    // checkLock(container2, homeNumber, unlockNext);
    if (curPage === "predndHome")
      checkLock(container2, homeNumber, unlockNext, "predndHome");
    else checkLock(container2, homeNumber, unlockNext);
    updateList({
      container: container2,
    });
  };

  return (
    <div>
      <Title title={title} name={id} />
      <div
        ref={drop}
        className={className}
        style={{
          border: `${border2}`,
        }}
      >
        {container
          .filter((item) => {
            return item.container === id;
          })
          .map((item, i) => {
            return (
              <DragItem
                tabindex={tabIndex + i * 5}
                item={item}
                key={item.id}
                deleteIcon={deleteIcon}
              />
            );
          })}
      </div>
    </div>
  );
};

DropItem.defaultProps = {
  title: "",
  border: "none",
};

const mapStateToProps = (state) => {
  return {
    currentList: state.currentList,
    homeNumber: state.homeNumber,
    currentPage: state.currentPage,
  };
};

const mapDispatchtoProps = (dispatch) => {
  return {
    updateList: (curList) => dispatch(currentList(curList)),
    unlockNext: (unlock) => dispatch(unlockNext(unlock)),
  };
};

export default connect(mapStateToProps, mapDispatchtoProps)(DropItem);
