export const next = (curPage) => ({
  type: "CLICK_NEXT",
  payload: curPage,
});

export const currentPage = (curPage) => ({
  type: "CURRENT_PAGE",
  payload: curPage,
});

export const currentList = (curList) => ({
  type: "CURRENT_LIST",
  payload: curList,
});

export const homeNumber = (num) => ({
  type: "HOME_NUMBER",
  payload: num,
});

export const unlockNext = (unlock) => ({
  type: "UNLOCK_NEXT",
  payload: unlock,
});

export const updateTitle = (title) => ({
  type: "UPDATE_TITLE",
  payload: title,
});

export const showRadio = (radio) => ({
  type: "SHOW_RADIO",
  payload: radio,
});
