import React from "react";
import { currentList } from "../../store/actions";
import { connect } from "react-redux";
import IconCheck from "../../icons/checkbox/IconCheck";
import IconLists from "../../lists/IconLists";

const CareLists = (props) => {
  const { currentList, id, title, className, updateList } = props;
  let container = [...currentList.container];

  const checkIcon = (id) => {
    console.log(id);
    for (let i = 0, j = container.length; i < j; i++) {
      if (container[i].id === id) {
        if (container[i].caretaker && container[i].caretaker === true)
          container[i].caretaker = false;
        else container[i].caretaker = true;
      }
    }
    updateList({
      container,
    });
    console.log("container:", container);
  };

  let tabIndex = 1;
  if (title === "Home1") tabIndex = 100;
  if (title === "Home2") tabIndex = 200;

  return (
    <div>
      <IconLists title={title} className={className} id={id}>
        {container
          .filter((item) => {
            return item.container === id;
          })
          .filter((item) => {
            return item.name.toLowerCase() !== "me";
          })
          .map((item, i) => {
            const faded =
              item.older18 && item.older18 === true ? "false" : "true";
            const checked =
              item.caretaker && item.caretaker === true ? "true" : "false";
            return (
              <IconCheck
                tabindex={tabIndex + i}
                item={item}
                key={item.id}
                checkIcon={checkIcon}
                faded={faded}
                checked={checked}
              />
            );
          })}
      </IconLists>
    </div>
  );
};

const mapStateToProps = (state) => {
  return {
    currentList: state.currentList,
  };
};

const mapDispatchtoProps = (dispatch) => {
  return {
    updateList: (curList) => dispatch(currentList(curList)),
  };
};

export default connect(mapStateToProps, mapDispatchtoProps)(CareLists);
