import React, { useEffect } from "react";
// import { connect } from "react-redux";
import { currentPage } from "../../store/actions";
import { Flex, Box, Text, Paragraph } from "@coreym/benchmark";
import MotionBox from "../../motions/motionBox/MotionBox";
import DisclosureControls from "./DisclosureControls";
import SummaryContent from "./SummaryContent";
import { useHistory } from "react-router";
import { useSelector, useDispatch } from "react-redux";

const tempVariants = {
  initial: {
    opacity: 0,
  },
  animate: {
    opacity: 1,
    transition: {
      delay: 0.5,
    },
  },
  exit: {
    opacity: 0,
    // transition: {
    //   duration: 1,
    // },
  },
};

function Summary(props) {
  const history = useHistory();
  const dispatch = useDispatch();
  const homeNumber = useSelector((state) => state.homeNumber);
  const homeTitle = useSelector((state) => state.updateTitle);

  useEffect(() => {
    dispatch(
      currentPage({
        stage: 4,
        page: "summary",
        mapProps: props,
      })
    );
  }, []);

  let page = null;
  switch (homeNumber.number) {
    case "1":
      page = (
        <>
          <Flex justifyContent="center">
            <DisclosureControls id="home1" title={homeTitle.home1}>
              <SummaryContent id="home1" />
            </DisclosureControls>
          </Flex>
        </>
      );
      break;
    case "2":
      page = (
        <>
          <Flex justifyContent="center">
            <DisclosureControls id="home1" title={homeTitle.home1}>
              <SummaryContent id="home1" />
            </DisclosureControls>
          </Flex>
          <Flex justifyContent="center" mt="2">
            <DisclosureControls id="home2" title={homeTitle.home2}>
              <SummaryContent id="home2" />
            </DisclosureControls>
          </Flex>
        </>
      );
      break;
    default:
      break;
  }
  return (
    <MotionBox
      variants={tempVariants}
      initial="initial"
      animate="animate"
      exit="exit"
    >
      <MotionBox p="5">
        <Flex justifyContent="center">
          <Paragraph>
            Here's what you told us about where you live.
            <br />
            <Text>Tap </Text>
            <Text fontWeight="bold">Next</Text>
            <Text> if everything looks correct.</Text>
          </Paragraph>
        </Flex>
      </MotionBox>

      {/* <Flex justifyContent="center">
        <DisclosureControls id="home1" title={homeTitle.home1}>
          <SummaryContent id="home1" />
        </DisclosureControls>
      </Flex> */}
      <Box>{page}</Box>
    </MotionBox>
  );
}

export default Summary;

