import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import { currentPage, unlockNext } from "../../store/actions";
import {
  Box,
  Flex,
  Image,
  Paragraph,
  Button,
  Columns,
  Column,
  Icon,
  NextButton,
  SingleSelect,
  MultipleSelectChoice,
  SingleSelectChoice,
  ZonesBackground,
  Text,
  Zone,
  Global,
  ThemeProvider,
} from "@coreym/benchmark";

import { motion, AnimatePresence } from "framer-motion";
import MotionBox from "../../motions/motionBox/MotionBox";

const buildingVariants = {
  initial: {
    scale: 0.5,
    x: "250px",
    y: "-330px",
  },
  animate: {
    scale: 0.5,
    x: "250px",
    y: "-330px",
  },
  exit: {
    scale: 1,
    x: "0",
    y: "0",
    transition: {
      duration: 1,
      delay: 1,
    },
  },
};

const textOut = {
  initial: {
    opacity: 1,
  },
  animate: {
    opacity: 1,
  },
  exit: {
    opacity: 0,
    transition: {
      duration: 1,
    },
  },
};

function Home(props) {
  const start = () => {
    props.history.push("/choseHome");
  };

  useEffect(() => {
    const { currentPage, unlockNext } = props;
    currentPage({
      page: "home",
      mapProps: props,
    });

    unlockNext({ unlock: true });
  }, []);

  const [showTest, setShowTest] = useState(true);

  const textTest = {
    initial: {
      x: "0",
    },
    animate: {
      x: "200px",
    },
  };

  return (
    <Box p={5}>
      <MotionBox
        variants={textOut}
        initial="initial"
        animate="animate"
        exit="exit"
      >
        <Flex justifyContent="center">
          <Paragraph>
            In this section, we'll ask about where you live <br />
            and who lives with you.
          </Paragraph>
        </Flex>
      </MotionBox>

      <div className="buildingTop">
        <motion.div
          className="buildings"
          variants={buildingVariants}
          initial="initial"
          animate="animate"
          exit="exit"
        >
          <img className="img1" src="img/Family structure_2_RowHouse.svg" />
          <img className="img2" src="img/Family structure_2_House.svg" />
          <img className="img4" src="img/Family structure_2_Apartment.svg" />
          <img className="img3" src="img/Family structure_2_Trailer.svg" />
        </motion.div>
      </div>
    </Box>
  );
}

const mapDispatchtoProps = (dispatch) => {
  return {
    currentPage: (curPage) => dispatch(currentPage(curPage)),
    unlockNext: (unlock) => dispatch(unlockNext(unlock)),
  };
};

export default connect(null, mapDispatchtoProps)(Home);
