import React, { useState } from "react";
import { updateTitle } from "../store/actions";
import { connect } from "react-redux";

function Title({ title = "Home", titleClass = "title", name, ...props }) {
  const [textDisplay, setTextDisplay] = useState("none");
  const onClickHandler = () => {
    let d = textDisplay === "none" ? "inline" : "none";
    setTextDisplay(d);
  };
  const onKeyDownHandler = (e) => {
    if (e.keyCode === 13) {
      // console.log(e.target.value);
      props.updateTitle({ [name]: e.target.value });
      e.target.value = "";
      setTextDisplay("none");
    }
  };
  const editIcon =
    title === "" ? null : (
      <span style={{ margin: "auto 10px" }}>
        <i
          className="fa fa-pencil"
          aria-hidden="true"
          onClick={onClickHandler}
        ></i>
        <input
          type="text"
          style={{ width: "150px", height: "18px", display: `${textDisplay}` }}
          onKeyDown={onKeyDownHandler}
        />
      </span>
    );
  return (
    <>
      {/* <Flex> */}
      <div className={titleClass}>
        {title}
        {editIcon}
      </div>
      {/* </Flex> */}
    </>
  );
}

const mapDispatchtoProps = (dispatch) => {
  return {
    updateTitle: (title) => dispatch(updateTitle(title)),
  };
};

export default connect(null, mapDispatchtoProps)(Title);

