import React from "react";
import { Text, Box, Flex } from "@coreym/benchmark";

function RemoveButton({
  fontSize = "2",
  fontWeight = "medium",
  color = "primary",
  ...props
}) {
  return (
    <Box css={{ cursor: "pointer" }}>
      <Flex justifyContent="center">
        <i className="fa fa-trash iconRemove" aria-hidden="true"></i>
        <Text pl="2" fontSize={fontSize} fontWeight={fontWeight} color={color}>
          Remove
        </Text>
      </Flex>
    </Box>
  );
}

export default RemoveButton;
