import React, { useRef } from "react";
import { connect } from "react-redux";
// import { Box, Flex, Image, Badge, Text } from "@coreym/benchmark";
import { useDrag } from "react-dnd";
import { ItemTypes } from "./ItemTypes";
import CharIcon from "../icons/charIcon/CharIcon";

const DragItem = ({ item, tabindex, deleteIcon = () => {}, ...props }) => {
  const closeRef = useRef();

  const curPage = props.currentPage.page;
  const removeIcon = () => {
    deleteIcon(item.id);
  };

  const keyDownRemove = (e) => {
    if (e.keyCode === 13) {
      console.log("remove...");
      removeIcon();
    }
  };

  const closeIcon =
    item.container === "draggers" ||
    item.name === "Me" ||
    (curPage === "dndHome" && item.container === "draggers2") ? null : (
      <i
        tabIndex="0"
        ref={closeRef}
        className="fa fa-times-circle iconClose fa-2x"
        aria-hidden="true"
        onKeyDown={keyDownRemove}
        onClick={removeIcon}
      ></i>
    );

  const [{ isDragging }, drag] = useDrag({
    item: {
      type: ItemTypes.CARD,
      id: item.id,
      name: item.name,
      url: item.url,
      container: item.container,
    },
    collect: (monitor) => ({
      isDragging: monitor.isDragging(),
    }),
  });

  const focusHandler = (e) => {
    e.target.style.outline = "1px dotted";
    e.target.style.outlineOffset = 2;
  };
  const blurHandler = (e) => {
    e.target.style.outline = "none";
  };
  const keyPressHandler = (e) => {
    // if (e.keyCode === 13) {
    //   console.log("drag...");
    // }
    if (e.keyCode === 39 && closeIcon !== null) {
      console.log(closeRef.current);
      closeRef.current.focus();
    }
  };

  return (
    <>
      <div
        id={item.id}
        tabIndex={tabindex}
        className="dragItem"
        ref={drag}
        onFocus={focusHandler}
        onBlur={blurHandler}
        onKeyDown={keyPressHandler}
        style={{
          backgroundColor: `${isDragging ? "lightgrey" : "white"}`,
          opacity: `${isDragging ? "0.2" : "1"}`,
        }}
      >
        <CharIcon url={item.url} name={item.name} />
        {closeIcon}
      </div>
    </>
  );
};

const mapStateToProps = (state) => {
  return {
    currentPage: state.currentPage,
  };
};

export default connect(mapStateToProps, null)(DragItem);
