import React, { useEffect } from "react";
import { connect } from "react-redux";
import { currentPage, unlockNext } from "../../store/actions";
import { DndProvider } from "react-dnd";
import { HTML5Backend } from "react-dnd-html5-backend";
import DropItem from "../../dragndrop/DropItem";
import { Flex, Box, Paragraph, Text } from "@coreym/benchmark";
import { checkLock } from "../../common/commons";
import { motion } from "framer-motion";
import MotionBox from "../../motions/motionBox/MotionBox";

const tempVariants = {
  initial: {
    opacity: 0,
  },
  animate: {
    opacity: 1,
    transition: {
      delay: 0.5,
    },
  },
  exit: {
    opacity: 0,
    // transition: {
    //   duration: 1,
    // },
  },
};

function DndHome(props) {
  useEffect(() => {
    const { currentPage, currentList, homeNumber, unlockNext } = props;
    currentPage({
      stage: 2,
      page: "dndHome",
      mapProps: props,
    });

    const container = [...currentList.container];
    checkLock(container, homeNumber, unlockNext);
  }, []);

  const { homeNumber, homeTitle } = props;
  // console.log("homenumber:", homeNumber);
  let page = null;
  switch (homeNumber.number) {
    case "0":
      //redirect...
      break;
    case "1":
      page = (
        // <div className="dnd-container1">
        <>
          <Flex justifyContent="center" pb="3">
            <DropItem className="draggers" id="draggers" />
          </Flex>

          <Flex justifyContent="center">
            <DropItem
              // border="1px solid #bcbcbc"
              title="Home1"
              className="home1"
              id="home1"
              over="solid 1px #0175d2"
            />
          </Flex>
        </>
        // </div>
      );
      break;
    case "2":
      page = (
        // <div className="dnd-container2">
        <>
          <Flex justifyContent="center" pb="3">
            <DropItem className="draggers" id="draggers2" />
          </Flex>
          <Flex justifyContent="center">
            <DropItem
              // border="1px solid #bcbcbc"
              title={homeTitle.home1}
              className="home1"
              id="home1"
              over="solid 1px #0175d2"
            />
            <DropItem
              // border="1px solid #bcbcbc"
              title={homeTitle.home2}
              className="home1"
              id="home2"
              over="solid 1px #0175d2"
            />
          </Flex>
        </>
      );
      break;
    default:
      break;
  }
  return (
    <MotionBox
      p="5"
      variants={tempVariants}
      initial="initial"
      animate="animate"
      exit="exit"
    >
      <DndProvider backend={HTML5Backend}>
        <Paragraph>
          <Text fontWeight="bold">Drag people</Text>
          <Text>
            {" "}
            to show where they live with you: in Home 1, Home 2, or in both
            homes.
          </Text>
        </Paragraph>
        <Box>{page}</Box>
      </DndProvider>
    </MotionBox>
  );
}

const mapStateToProps = (state) => {
  return {
    homeNumber: state.homeNumber,
    currentList: state.currentList,
    homeTitle: state.updateTitle,
  };
};

const mapDispatchtoProps = (dispatch) => {
  return {
    currentPage: (curPage) => dispatch(currentPage(curPage)),
    unlockNext: (unlock) => dispatch(unlockNext(unlock)),
  };
};

export default connect(mapStateToProps, mapDispatchtoProps)(DndHome);
