import React, { useEffect } from "react";
import { connect } from "react-redux";
import { currentPage, unlockNext } from "../../store/actions";
import { DndProvider } from "react-dnd";
import { HTML5Backend } from "react-dnd-html5-backend";
import DropItem from "../../dragndrop/DropItem";
import { Flex, Box, Paragraph, Text } from "@coreym/benchmark";
import { checkLock } from "../../common/commons";
import { motion } from "framer-motion";
import MotionBox from "../../motions/motionBox/MotionBox";

const tempVariants = {
  initial: {
    opacity: 0,
  },
  animate: {
    opacity: 1,
    transition: {
      delay: 0.5,
    },
  },
  exit: {
    opacity: 0,
    // transition: {
    //   duration: 1,
    // },
  },
};

function PreDndHome(props) {
  useEffect(() => {
    const { currentPage, currentList, homeNumber, unlockNext } = props;
    currentPage({
      stage: 2,
      page: "predndHome",
      mapProps: props,
    });

    const container = [...currentList.container];
    checkLock(container, homeNumber, unlockNext, "prehome");

    if (homeNumber.number === "1") props.history.push("/dndHome");
  }, []);

  const { homeNumber, homeTitle } = props;
  // console.log("homenumber:", homeNumber);
  let page = null;
  switch (homeNumber.number) {
    case "0":
      //redirect...
      break;
    case "1":
      //redirect to DndHome page...
      // props.history.push("/dndHome");
      break;
    case "2":
      page = (
        <>
          <Flex justifyContent="center" pb="3">
            <DropItem className="draggers" id="draggers" />
          </Flex>
          <Flex justifyContent="center">
            <DropItem
              title={"Who lives with you"}
              className="prehome"
              id="draggers2"
              over="solid 1px #0175d2"
            />
          </Flex>
        </>
      );
      break;
    default:
      break;
  }
  return (
    <MotionBox
      p="5"
      variants={tempVariants}
      initial="initial"
      animate="animate"
      exit="exit"
    >
      <DndProvider backend={HTML5Backend}>
        <Paragraph>
          Think about the two homes where you spend the most time.
          <br />
          <Text fontWeight="bold">Drag the people</Text>
          <Text> who live with you in those homes to the box.</Text>
        </Paragraph>
        <Box>{page}</Box>
      </DndProvider>
    </MotionBox>
  );
}

const mapStateToProps = (state) => {
  return {
    homeNumber: state.homeNumber,
    currentList: state.currentList,
    homeTitle: state.updateTitle,
  };
};

const mapDispatchtoProps = (dispatch) => {
  return {
    currentPage: (curPage) => dispatch(currentPage(curPage)),
    unlockNext: (unlock) => dispatch(unlockNext(unlock)),
  };
};

export default connect(mapStateToProps, mapDispatchtoProps)(PreDndHome);
