import React from "react";
import { Box, Text, Flex, Stack } from "@coreym/benchmark";
import CareIcon from "./CareIcon";
import CharIcon from "../../icons/charIcon/CharIcon";
import { useSelector } from "react-redux";

function SummaryContent({
  fontSize = "2",
  fontWeight = "normal",
  id,
  ...props
}) {
  const currentList = useSelector((state) => state.currentList);
  const { container } = currentList;

  return (
    <Box p="2">
      <Text fontSize={fontSize} fontWeight={fontWeight}>
        People who take care of you
      </Text>
      <Box pt="3">
        <Flex>
          {container
            .filter((item) => {
              return item.container === id && item.caretaker;
            })
            .map((item) => {
              return <CareIcon item={item} key={item.id} />;
            })}
        </Flex>
      </Box>
      <Text fontSize={fontSize} fontWeight={fontWeight}>
        Other people who live with you
      </Text>
      <Box pt="3" width="80px">
        <Flex>
          {container
            .filter((item) => {
              return item.container === id && !item.caretaker;
            })
            .map((item) => {
              return <CharIcon url={item.url} name={item.name} key={item.id} />;
            })}
        </Flex>
      </Box>
    </Box>
  );
}

export default SummaryContent;
