import React from "react";
import Title from "../title/Title";

const IconLists = ({
  title = "Home",
  className = "home1",
  id,
  children,
  ...props
}) => {
  return (
    <>
      <Title title={title} name={id} />
      {/* <div className="title">{title}</div> */}
      <div className={className}>{children}</div>
    </>
  );
};

export default IconLists;
