import { combineReducers } from "redux";
import {
  nextReducer,
  currentPageReducer,
  currentListReducer,
  homeNumberReducer,
  unlockNextReducer,
  updateTitleReducer,
  showRadioReducer,
} from "./reducers";

const rootReducer = combineReducers({
  next: nextReducer,
  currentPage: currentPageReducer,
  currentList: currentListReducer,
  homeNumber: homeNumberReducer,
  unlockNext: unlockNextReducer,
  updateTitle: updateTitleReducer,
  showRadio: showRadioReducer,
});

export default rootReducer;
