import React, { useState } from "react";
import { Text, Box, Flex } from "@coreym/benchmark";

function HomeButton({
  fontSize = "2",
  fontWeight = "medium",
  color = "primary",
  title = "Home",
  opened = true,
  toggleContent = () => {},
  ...props
}) {
  const [arrow, setArrow] = useState(opened ? "down" : "right");

  const clickHandler = () => {
    setArrow(arrow === "down" ? "right" : "down");
    toggleContent();
  };
  return (
    <Box onClick={clickHandler} css={{ cursor: "pointer" }}>
      <Flex justifyContent="center">
        <i className={`fa fa-caret-${arrow} iconRemove`} aria-hidden="true"></i>
        <Text pl="2" fontSize={fontSize} fontWeight={fontWeight} color={color}>
          {title}
        </Text>
      </Flex>
    </Box>
  );
}

export default HomeButton;
