import homeData from "../common/homeData";

const initState = {};

export const nextReducer = (state = initState, action) => {
  switch (action.type) {
    case "CLICK_NEXT":
      console.log("next clicked...");
      action.payload.push("/page2");
      return state;

    default:
      return state;
  }
};

const initCurPageState = {};

export const currentPageReducer = (state = initCurPageState, action) => {
  switch (action.type) {
    case "CURRENT_PAGE":
      return action.payload;

    default:
      return state;
  }
};

const initCurListState = {
  container: homeData,
  // draggers: homeData,
  // home1: [],
  // home2: [],
};

export const currentListReducer = (state = initCurListState, action) => {
  switch (action.type) {
    case "CURRENT_LIST":
      return {
        ...state,
        ...action.payload,
      };

    default:
      return state;
  }
};

