import React, { useEffect } from "react";
import { connect } from "react-redux";
import { currentPage } from "../../store/actions";
import { Flex, Box, Paragraph, Text } from "@coreym/benchmark";
import CheckLists from "./CheckLists";
import { motion } from "framer-motion";
import MotionBox from "../../motions/motionBox/MotionBox";

const tempVariants = {
  initial: {
    opacity: 0,
  },
  animate: {
    opacity: 1,
    transition: {
      delay: 0.5,
    },
  },
  exit: {
    opacity: 0,
    // transition: {
    //   duration: 1,
    // },
  },
};

function YourFamily(props) {
  useEffect(() => {
    props.currentPage({
      stage: 2,
      page: "yourfamily",
      mapProps: props,
    });
  }, []);

  const { homeNumber, homeTitle } = props;
  console.log("homenumber:", homeNumber);
  let page = null;
  switch (homeNumber.number) {
    case "1":
      page = (
        // <div className="dnd-container1">
        <>
          <Flex justifyContent="center">
            <CheckLists
              id="home1"
              title={homeTitle.home1}
              className="home1"
            ></CheckLists>
          </Flex>
        </>
        // </div>
      );
      break;
    case "2":
      page = (
        // <div className="dnd-container2">
        <>
          <Flex justifyContent="center">
            <CheckLists
              id="home1"
              title={homeTitle.home1}
              className="home1"
            ></CheckLists>
            <CheckLists
              id="home2"
              title={homeTitle.home2}
              className="home1"
            ></CheckLists>
          </Flex>
        </>
      );
      break;
    default:
      break;
  }
  return (
    <MotionBox
      p="5"
      variants={tempVariants}
      initial="initial"
      animate="animate"
      exit="exit"
    >
      <Paragraph>
        <Text>Who in your homes is </Text>
        <Text fontWeight="bold">18 years or older?</Text>
      </Paragraph>
      <Box>{page}</Box>
    </MotionBox>
  );
}

const mapStateToProps = (state) => {
  return {
    homeNumber: state.homeNumber,
    homeTitle: state.updateTitle,
  };
};

const mapDispatchtoProps = (dispatch) => {
  return {
    currentPage: (curPage) => dispatch(currentPage(curPage)),
  };
};

export default connect(mapStateToProps, mapDispatchtoProps)(YourFamily);
