import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import { currentPage, homeNumber, unlockNext } from "../../store/actions";
import {
  Flex,
  Box,
  Text,
  SingleSelect,
  SingleSelectChoice,
  Modal,
  Paragraph,
  Stack,
} from "@coreym/benchmark";
import "./ChoseHome.css";
import { motion } from "framer-motion";
import MotionBox from "../../motions/motionBox/MotionBox";

const buildingVariants = {
  initial: {
    opacity: 0,
    scale: 1,
    x: "0",
    y: "0",
  },
  animate: {
    opacity: 1,
    scale: 1,
    x: "0",
    y: "0",
    transition: {
      type: "easeOut",
      duration: 0,
      delay: 2,
    },
  },
};

const imgTransition = {
  type: "easeOut",
  duration: 1,
  delay: 3,
};

const imgVariants1 = {
  initial: {
    opacity: 1,
    x: "0",
    y: "0",
  },
  animate: {
    opacity: 0.5,
    x: "-400px",
    y: "40px",
    transition: imgTransition,
  },
};

const imgVariants2 = {
  initial: {
    opacity: 1,
    x: "0",
    y: "0",
  },
  animate: {
    opacity: 0.5,
    x: "50px",
    y: "10px",
    transition: imgTransition,
  },
};

const imgVariants3 = {
  initial: {
    opacity: 1,
    x: "0",
    y: "0",
  },
  animate: {
    opacity: 0.5,
    x: "100px",
    y: "40px",
    transition: imgTransition,
  },
};

const imgVariants4 = {
  initial: {
    opacity: 1,
    x: "0",
    y: "0",
  },
  animate: {
    opacity: 0.5,
    x: "-50px",
    y: "10px",
    transition: imgTransition,
  },
};

const textVariants = {
  initial: {
    opacity: 0,
  },
  animate: {
    opacity: 1,
    transition: {
      type: "easeOut",
      duration: 1,
      delay: 4,
    },
  },
};

const tempVariants = {
  initial: {
    opacity: 1,
  },
  animate: {
    opacity: 1,
  },
  exit: {
    opacity: 0,
    // transition: {
    //   duration: 1,
    // },
  },
};

function ChoseHome(props) {
  const { currentPage, homeOption, unlockNext } = props;
  let selectedOption = homeOption.number;

  const [introTextShow, setIntroTextShow] = useState(true);

  useEffect(() => {
    currentPage({
      stage: 1,
      page: "choseHome",
      mapProps: props,
    });
    if (selectedOption === null) unlockNext({ unlock: false });
    else unlockNext({ unlock: true });
  }, []);

  const onChangeHandler = (option) => {
    const { homeNumber, unlockNext } = props;
    homeNumber({
      number: option,
    });
    unlockNext({ unlock: true });
  };

  return (
    <MotionBox
      p={5}
      variants={tempVariants}
      initial="initial"
      animate="animate"
      exit="exit"
    >
      <MotionBox variants={textVariants} initial="initial" animate="animate">
        <Flex justifyContent="center">
          <Paragraph>
            There are differnt kind of homes,
            <br />
            like house, apartments, and trailer homes.
            <br />
            <Text fontWeight="bold">How many homes</Text>
            <Text> do you live in?</Text>
          </Paragraph>
        </Flex>
        <Flex justifyContent="center">
          <SingleSelect
            hasCustomLayout
            onChange={onChangeHandler}
            selected={selectedOption}
          >
            <Stack>
              <SingleSelectChoice value="2">2 or more homes</SingleSelectChoice>
              <SingleSelectChoice value="1">1 home</SingleSelectChoice>
              <SingleSelectChoice value="0">No home</SingleSelectChoice>
            </Stack>
          </SingleSelect>
        </Flex>
      </MotionBox>
      <div className="buildingTop">
        <motion.div
          className="buildings"
          variants={buildingVariants}
          initial="initial"
          animate="animate"
        >
          <motion.img
            variants={imgVariants1}
            className="img1"
            src="img/Family structure_2_RowHouse.svg"
          />
          <motion.img
            variants={imgVariants2}
            className="img2"
            src="img/Family structure_2_House.svg"
          />
          <motion.img
            variants={imgVariants3}
            className="img4"
            src="img/Family structure_2_Apartment.svg"
          />
          <motion.img
            variants={imgVariants4}
            className="img3"
            src="img/Family structure_2_Trailer.svg"
          />
        </motion.div>
      </div>
    </MotionBox>
  );
}

const mapStateToProps = (state) => {
  return {
    homeOption: state.homeNumber,
  };
};

const mapDispatchtoProps = (dispatch) => {
  return {
    currentPage: (curPage) => dispatch(currentPage(curPage)),
    homeNumber: (num) => dispatch(homeNumber(num)),
    unlockNext: (unlock) => dispatch(unlockNext(unlock)),
  };
};

export default connect(mapStateToProps, mapDispatchtoProps)(ChoseHome);
