import React, { Component } from "react";
import { connect } from "react-redux";
import nextData from "../common/nextData";
import { unlockNext, showRadio } from "../store/actions";
import { Toolbar } from "@coreym/benchmark";
// import { useHistory } from "react-router";

class ToolbarApp extends Component {
  static next() {
    console.log("static next function...");
    this.next_inside();
  }

  next_inside = () => {
    const { currentPage, showRadio, stepStatus } = this.props;
    const curPage = currentPage.page;
    const index = nextData.indexOf(curPage);
    const step = stepStatus.step + 1;
    const totalSteps = stepStatus.totalSteps;

    if (curPage === "caretakerperson") {
      if (step <= totalSteps)
        showRadio({ showRadio2: false, setClass: true, step });
      else currentPage.mapProps.history.push(`/${nextData[index + 1]}`);
    } else currentPage.mapProps.history.push(`/${nextData[index + 1]}`);
  };

  prev_inside = () => {
    console.log("prev...");
    const { currentPage } = this.props;
    const curPage = currentPage.page;
    const index = nextData.indexOf(curPage);
    if (index > 0) {
      if (nextData[index - 1] === "home")
        currentPage.mapProps.history.push("/");
      else currentPage.mapProps.history.push(`/${nextData[index - 1]}`);
    }
  };

  render() {
    let { unlockNextButton } = this.props;
    let disNext = !unlockNextButton.unlock;
    return (
      <div>
        <Toolbar
          isNextDisabled={disNext}
          onClickPrev={this.prev_inside}
          onClickNext={this.next_inside}
        />
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    currentPage: state.currentPage,
    homeNumber: state.homeNumber,
    unlockNextButton: state.unlockNext,
    stepStatus: state.showRadio,
  };
};
const mapDispatchtoProps = (dispatch) => {
  return {
    unlockNext: (unlock) => dispatch(unlockNext(unlock)),
    showRadio: (radio) => dispatch(showRadio(radio)),
  };
};
export default connect(mapStateToProps, mapDispatchtoProps)(ToolbarApp);
