import React from "react";

const Bar = ({ color = "grey", ...props }) => {
  const bgcolor = color === "grey" ? "#ababab" : "#188cff";
  return (
    <div
      style={{
        backgroundColor: `${bgcolor}`,
        width: "150px",
        height: "3px",
        margin: "auto 0",
      }}
    ></div>
  );
};

export default Bar;
