import React from "react";

const ImgHolder = () => {
  return (
    <div
      style={{
        backgroundColor: "grey",
        width: "50px",
        height: "50px",
        borderRadius: "25px",
      }}
    ></div>
  );
};

const CharIcon = ({
  url,
  name = "Name",
  width = "50px",
  alt = "character",
  faded = "false",
  ...props
}) => {
  const grayscale =
    faded === "true" || name === "Me" ? "grayscale(100%)" : "grayscale(0%)";
  const imgH = url ? (
    <img src={url} alt={alt} width={width} style={{ filter: `${grayscale}` }} />
  ) : (
    <ImgHolder />
  );
  return (
    <>
      <center>
        {imgH}
        <div className="charText">{name}</div>
      </center>
    </>
  );
};

export default CharIcon;
