import React, { useState } from "react";
import { currentList } from "../../store/actions";
import { connect } from "react-redux";
import IconCheck from "../../icons/checkbox/IconCheck";
import IconLists from "../../lists/IconLists";
import TextCheck from "../../icons/checkbox/TextCheck";

const CheckLists = (props) => {
  const { currentList, id, title, className, updateList } = props;
  let container = [...currentList.container];

  const [textChecked, setTextChecked] = useState("false");

  const checkIcon = (id) => {
    // console.log(id);
    for (let i = 0, j = container.length; i < j; i++) {
      if (container[i].id === id) {
        if (container[i].older18 && container[i].older18 === true)
          container[i].older18 = false;
        else {
          container[i].older18 = true;
          setTextChecked("false");
        }
      }
    }
    updateList({
      container,
    });
    // console.log("container:", container);
  };

  const checkText = (checked) => {
    if (checked === "true") {
      for (let i = 0, j = container.length; i < j; i++) {
        if (container[i].container === id) container[i].older18 = false;
      }
      console.log(container);
      updateList({
        container,
      });
    }
    // console.log(checked);
  };

  let tabIndex = 1;
  if (title === "Home1") tabIndex = 100;
  if (title === "Home2") tabIndex = 200;

  return (
    <div>
      <IconLists title={title} className={className} id={id}>
        {container
          .filter((item) => {
            return item.container === id;
          })
          .map((item, i) => {
            const checked =
              item.older18 && item.older18 === true ? "true" : "false";
            return (
              <IconCheck
                checked={checked}
                tabindex={tabIndex + i}
                item={item}
                key={item.id}
                checkIcon={checkIcon}
                // faded="true"
              />
            );
          })}
        {/* <div style={{ position: "absolute", bottom: "10px", left: "20px" }}>
          <TextCheck checked={textChecked} checkText={checkText} />
        </div> */}
      </IconLists>
    </div>
  );
};

const mapStateToProps = (state) => {
  return {
    currentList: state.currentList,
  };
};

const mapDispatchtoProps = (dispatch) => {
  return {
    updateList: (curList) => dispatch(currentList(curList)),
  };
};

export default connect(mapStateToProps, mapDispatchtoProps)(CheckLists);
