import React, { useState } from "react";
// import { Box, Flex, Image, Badge, Text } from "@coreym/benchmark";
import "./IconCheck.css";
// import PropTypes from "prop-types";
import CharIcon from "../charIcon/CharIcon";

const IconCheck = ({
  checked = "false",
  tabindex = "0",
  item,
  checkIcon = () => {},
  faded = "false",
  ...props
}) => {
  const gray = faded === "true" ? "true" : "false";
  const [bgcolor, setBgcolor] = useState(
    checked === "true" ? "#188cff" : "white"
  );
  const checkToggle = () => {
    // if (item.name.toLowerCase() !== "me" && faded !== "true") {
    if (faded !== "true") {
      if (bgcolor === "white") setBgcolor("#188cff");
      else setBgcolor("white");

      checkIcon(item.id);
    }
  };

  const icon =
    // faded === "true" || item.name.toLowerCase() === "me" ? null : (
    faded === "true" ? null : (
      <i
        className="fa fa-check fa-lg checkframe"
        style={{ backgroundColor: `${bgcolor}` }}
        aria-hidden="true"
      ></i>
    );

  return (
    <div
      id={item.id}
      tabIndex={tabindex}
      className="iconcheck"
      onClick={checkToggle}
    >
      <CharIcon url={item.url} name={item.name} faded={gray} />
      {icon}
    </div>
  );
};

export default IconCheck;
