import React, { useState, useEffect } from "react";
import "./IconCheck.css";

function TextCheck({
  checked = "false",
  tabindex = "0",
  label = "No one is under 18 years old.",
  checkText = () => {},
  ...props
}) {
  const [bgcolor, setBgcolor] = useState(
    checked === "true" ? "#188cff" : "white"
  );
  const checkToggle = () => {
    if (bgcolor === "white") {
      setBgcolor("#188cff");
      checked = "true";
    } else {
      setBgcolor("white");
      checked = "false";
    }

    checkText(checked);
  };
  useEffect(() => {
    setBgcolor(checked === "true" ? "#188cff" : "white");
    console.log("bgcolor:", bgcolor);
  }, [checked]);

  return (
    <div onClick={checkToggle} className="textCheck">
      <i
        className="fa fa-check textcheckframe"
        style={{ backgroundColor: `${bgcolor}`, fontSize: "16px" }}
        aria-hidden="true"
      ></i>
      <span>{label}</span>
    </div>
  );
}

export default TextCheck;
