import ToolbarApp from "../nav/ToolbarApp";

class FamilyAPI {
  // constructor() {}

  init() {
    if (window.parent.hook) {
      window.parent.hook(this);
    }
  }

  next() {
    console.log("next from api...");
    ToolbarApp.next();
  }

  sendData(data) {
    if (window.parent.getData) {
      window.parent.getData(data);
    }
  }
}

export default FamilyAPI;

