import React from "react";
import { Stack, Text, Paragraph } from "@coreym/benchmark";
import questionData from "../../common/questionData";

const ImgHolder = () => {
  return (
    <div
      style={{
        backgroundColor: "grey",
        width: "50px",
        height: "50px",
        borderRadius: "25px",
      }}
    ></div>
  );
};

const CareIcon = ({
  item,
  //   name = "Name",
  width = "50px",
  //   work = "Works for pay",
  //   education = "Graduated from college",
  alt = "character",
  faded = "false",
  ...props
}) => {
  const url = item.url;
  const name = item.name ? item.name : "Name";
  const work = item.work
    ? questionData.work[Number(item.work)]
    : "Works for pay";
  const education = item.education
    ? questionData.education[Number(item.education)]
    : "Graduated from college";
  const imgH = url ? <img src={url} alt={alt} width={width} /> : <ImgHolder />;
  return (
    <>
      <Stack direction="row">
        {imgH}
        <Paragraph>
          <Text fontSize="1">{name}</Text>
          <br />
          <Text fontSize="1" color="n.600">
            {work}
            <br />
            {education}
          </Text>
        </Paragraph>
      </Stack>
    </>
  );
};

export default CareIcon;
