import React, { useState } from "react";
import { Box, Flex } from "@coreym/benchmark";
import RemoveButton from "./RemoveButton";
import HomeButton from "./HomeButton";

function DisclosureControls({
  width = "700px",
  height = "400px",
  title = "Home",
  opened = true,
  children,
  ...props
}) {
  const [showContent, setShowContent] = useState(opened ? true : false);
  height = showContent ? "400px" : "auto";

  const toggleContent = () => {
    setShowContent(!showContent);
    height = showContent ? "400px" : "auto";
  };
  return (
    <Box
      p="3"
      width={width}
      height={height}
      border="1"
      borderRadius="md"
      borderColor="n.600"
    >
      <Flex justifyContent="space-between">
        {/* <button onClick={toggleContent}>{title}</button> */}
        <HomeButton
          opened={opened}
          toggleContent={toggleContent}
          title={title}
        />
        <RemoveButton></RemoveButton>
      </Flex>
      {showContent && children}
    </Box>
  );
}

export default DisclosureControls;
