import React from "react";
// import { Flex, Box, Text } from "@coreym/benchmark";

const NavIcon = ({ status = "1", text = "", ...props }) => {
  let one = "block",
    two = "none",
    three = "none",
    color = "grey";
  if (status === "2") {
    one = "none";
    two = "block";
    three = "none";
    color = "black";
  }
  if (status === "3") {
    one = "none";
    two = "none";
    three = "block";
  }
  return (
    <div style={{ position: "relative", color: `${color}` }}>
      {/* <center> */}
      <i
        className="fa fa-check-circle"
        style={{ color: "#188cff", display: `${three}` }}
        aria-hidden="true"
      ></i>
      <i
        className="fa fa-circle"
        style={{ color: "#188cff", display: `${two}` }}
        aria-hidden="true"
      ></i>
      <i
        className="fa fa-circle-thin"
        style={{ color: "#ababab", display: `${one}` }}
        aria-hidden="true"
      ></i>
      {/* </center> */}
      <div
        style={{
          position: "absolute",
          fontSize: "12px",
          width: "150px",
          textAlign: "center",
          left: "-68px",
        }}
      >
        {text}
      </div>
    </div>
  );
};

export default NavIcon;
