import React, { useEffect } from "react";
import { showRadio, currentList as updateList } from "../../store/actions";
import IconLists from "../../lists/IconLists";
import CharIcon from "../../icons/charIcon/CharIcon";
import { SingleSelect, SingleSelectChoice, Stack } from "@coreym/benchmark";
import { motion } from "framer-motion";
import { useSelector, useDispatch } from "react-redux";

const moveVariants = {
  initial: {
    x: "0",
  },
  animate: {
    x: "-1050px",
  },
};

function CarePersons({ id, title, className, ...props }) {
  const currentList = useSelector((state) => state.currentList);
  const careSteps = useSelector((state) => state.showRadio);
  const dispatch = useDispatch();

  let container = [...currentList.container];
  let { setClass, step, showRadio2 } = careSteps;

  const num1 = container.filter((item) => {
    return (
      item.container === "home1" &&
      item.name.toLowerCase() !== "me" &&
      item.caretaker
    );
  }).length;
  const num2 = container.filter((item) => {
    return (
      item.container === "home2" &&
      item.name.toLowerCase() !== "me" &&
      item.caretaker
    );
  }).length;

  let moveHome = step <= num1 ? false : true;

  const totalSteps = num1 + num2;
  useEffect(() => {
    dispatch(showRadio({ totalSteps }));
  }, []);

  let caretakers = container
    .filter((item) => {
      return (
        item.container === id &&
        item.name.toLowerCase() !== "me" &&
        item.caretaker
      );
    })
    .map((item, i) => {
      if (item.container === "home1") item.step = i + 1;
      if (item.container === "home2") item.step = i + 1 + num1;
      if (step !== 0) {
        item.faded = true;
        if (item.step === step) item.faded = false;
      }

      return item;
    });

  let tabIndex = 1;
  if (title === "Home1") tabIndex = 100;
  if (title === "Home2") tabIndex = 200;

  const workHandler = (option) => {
    for (let i = 0, j = container.length; i < j; i++) {
      if (container[i].step === step) {
        container[i].work = option;
        break;
      }
    }
    dispatch(updateList({ container }));
    dispatch(showRadio({ showRadio2: true }));
  };

  const educationHandler = (option) => {
    for (let i = 0, j = container.length; i < j; i++) {
      if (container[i].step === step) {
        container[i].education = option;
        break;
      }
    }
    dispatch(updateList({ container }));
  };

  return (
    <motion.div
      variants={moveVariants}
      animate={moveHome ? "animate" : "initial"}
    >
      <IconLists
        title={title}
        className={setClass ? "home4" : className}
        id={id}
      >
        <Stack spacing="2" direction="row">
          {caretakers.map((item) => {
            let showRadio = false;
            if (step !== 0) {
              showRadio = !item.faded;
            }
            let workOption = item.work ? item.work : null;
            let educationOption = item.education ? item.education : null;
            return (
              <div key={Math.random().toString()}>
                <CharIcon
                  url={item.url}
                  name={item.name}
                  key={item.id}
                  faded={item.faded ? "true" : "false"}
                />
                {showRadio && (
                  <div className="radios">
                    <p>
                      In the last month, has your <b>{item.name}</b> worked for
                      pay?
                    </p>
                    <SingleSelect
                      hasCustomLayout
                      onChange={workHandler}
                      selected={workOption}
                    >
                      <Stack spacing="2" direction="row">
                        <SingleSelectChoice value="0">Yes</SingleSelectChoice>
                        <SingleSelectChoice value="1">No</SingleSelectChoice>
                        <SingleSelectChoice value="2">
                          I don't know
                        </SingleSelectChoice>
                      </Stack>
                    </SingleSelect>
                    {showRadio2 && (
                      <div>
                        <p>
                          How far in school did your <b>{item.name}</b> go?
                        </p>

                        <SingleSelect
                          hasCustomLayout
                          onChange={educationHandler}
                          selected={educationOption}
                        >
                          {/* <Stack spacing="2" direction="row"> */}
                          <SingleSelectChoice value="0">
                            She didn't finish high school.
                          </SingleSelectChoice>
                          <SingleSelectChoice value="1">
                            She graduated from high school or has a high school
                            degree.
                          </SingleSelectChoice>
                          <SingleSelectChoice value="2">
                            She had some college but didn't graduate.
                          </SingleSelectChoice>
                          <SingleSelectChoice value="3">
                            She graduated from college.
                          </SingleSelectChoice>
                          <SingleSelectChoice value="4">
                            I don't know.
                          </SingleSelectChoice>
                        </SingleSelect>
                      </div>
                    )}
                  </div>
                )}
              </div>
            );
          })}
        </Stack>
      </IconLists>
    </motion.div>
  );
}

export default CarePersons;
