export const filterDuplicate = (container, dropper, name) => {
  let hasName = false;
  const container1 = container.filter((item) => item.container === dropper);
  for (let i = 0; i < container1.length; i++) {
    if (container1[i].name.toLowerCase() === name.toLowerCase()) {
      hasName = true;
      break;
    }
  }
  return hasName;
};

export const checkLock = (c, homeNumber, unlockNext, page = "home1") => {
  const home1 = c.filter((item) => {
    return item.container === "home1";
  });
  const home2 = c.filter((item) => {
    return item.container === "home2";
  });

  const prehome = c.filter((item) => {
    return item.container === "draggers2";
  });

  if (page === "predndHome") {
    if (prehome.length <= 1) unlockNext({ unlock: false });
    else unlockNext({ unlock: true });
  } else {
    if (homeNumber.number === "1") {
      if (home1.length === 1) unlockNext({ unlock: false });
      else unlockNext({ unlock: true });
    }
    if (homeNumber.number === "2") {
      if (home1.length === 1 || home2.length === 1)
        unlockNext({ unlock: false });
      else unlockNext({ unlock: true });
    }
  }
};

export const changeIconLabel = (droppedItem, container) => {
  const c = container
    .filter((item) => {
      return item.container === droppedItem.container;
    })
    .map((item) => item.name);
  const n = 10;
  let compName = droppedItem.name;
  for (let i = 2; i < n; i++) {
    if (c.includes(compName)) compName = droppedItem.name + i;
    else break;
  }
  return compName;
};
